body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
}

.App {
  margin: 0;
  padding: 0;
  text-align: center;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.banner{width:100vw;height:12vh;position:fixed;z-index:3;display:flex;align-items:center;justify-content:center;background:linear-gradient(180deg, #191919 0%, rgba(0,0,0,0.5) 100%)}.banner .logo_container,.banner .hamburger-container{width:20vw;height:100%;display:flex;align-items:center;justify-content:center}.banner .logo_container .logo{height:50%}.banner h1{font-family:"Montserrat",sans-serif;font-weight:600;font-size:1.5rem;color:#fff;transition:all 0.5s ease-in-out}.banner .hamburger-container{cursor:pointer;transition:all 0.5s ease-in-out}.banner .hamburger-container .hamburger,.banner .hamburger-container .hamburger::before,.banner .hamburger-container .hamburger::after{content:"";position:absolute;width:12vw;height:5px;background:#fff;border-radius:5px;box-shadow:0 2px 5px rgba(134,100,87,0.2);transition:all 0.5s ease-in-out}.banner .hamburger-container .hamburger::before{-webkit-transform:translate(-6vw, -2vh);transform:translate(-6vw, -2vh)}.banner .hamburger-container .hamburger::after{-webkit-transform:translate(-6vw, 2vh);transform:translate(-6vw, 2vh)}.falseDarkMode{background:linear-gradient(180deg, #fff 0%, #dcdcdc 100%)}.falseDarkMode h1{color:#000}.falseDarkMode .hamburger-container .hamburger{-webkit-transform:translateX(-50px);transform:translateX(-50px);background:transparent;box-shadow:none}.falseDarkMode .hamburger-container .hamburger::before{-webkit-transform:translateX(50%) rotate(45deg);transform:translateX(50%) rotate(45deg);background-color:black}.falseDarkMode .hamburger-container .hamburger::after{-webkit-transform:translateX(50%) rotate(-45deg);transform:translateX(50%) rotate(-45deg);background-color:black}@media screen and (min-width: 768px){.banner{height:15vh}.banner h1{font-size:2.5rem}.banner .hamburger-container .hamburger,.banner .hamburger-container .hamburger::before,.banner .hamburger-container .hamburger::after{width:4vw;height:8px}.banner .hamburger-container .hamburger::before{-webkit-transform:translate(-2vw, -2.5vh);transform:translate(-2vw, -2.5vh)}.banner .hamburger-container .hamburger::after{-webkit-transform:translate(-2vw, 2.5vh);transform:translate(-2vw, 2.5vh)}.falseDarkMode .hamburger-container .hamburger::before{-webkit-transform:translateX(50%) rotate(45deg);transform:translateX(50%) rotate(45deg)}.falseDarkMode .hamburger-container .hamburger::after{-webkit-transform:translateX(50%) rotate(-45deg);transform:translateX(50%) rotate(-45deg)}}

.dropdown{width:100%;height:35vh;margin-top:12vh;display:flex;position:fixed;flex-direction:column;z-index:2;background:linear-gradient(180deg, #dcdcdc 0%, #fff 12%);transition:all 0.5s linear;opacity:1}.dropdown .options-container ul{list-style-type:none;padding-top:2vh}.dropdown .options-container ul li button{margin:0;padding:0;cursor:pointer}.dropdown .options-container ul li button h3{font-family:"Montserrat",sans-serif;color:#000;font-weight:400;font-size:1.5rem;margin-top:4vh}.dropdown.falseActiveDropdown{-webkit-transform:translateX(200%);transform:translateX(200%)}@media (min-width: 768px){.dropdown{height:45vh;margin-top:15vh}.dropdown .options-container{margin-top:2vh}.dropdown .options-container ul li button h3{font-size:3rem;font-weight:300;margin-bottom:2vh}.dropdown.falseActiveDropdown{-webkit-transform:translateY(-200%);transform:translateY(-200%);opacity:0}}

.home{height:100vh;background-image:url(http://emijerochim.com/static/media/home.2ab36ffc.jpg);background-size:300%;background-position:center}.home .home-container{height:77vh;padding-top:20vh;display:flex;flex-direction:column}.home .home-container h2,.home .home-container p{font-family:"Montserrat",sans-serif;color:#fff}.home .home-container h2{font-size:2rem;font-weight:600}.home .home-container p{font-size:1rem;font-weight:500;margin-top:5vh;width:85%;background-color:rgba(0,0,0,0.2);border-radius:10px}@media screen and (min-width: 768px){.home{background-size:cover;background-position:25% 63%}.home .home-container{height:80vh;padding-top:23vh}.home .home-container h2{font-size:4rem}.home .home-container p{font-size:2rem;width:75%}}

.social-media{list-style-type:none;display:flex;width:90%;margin-top:8vh;margin-bottom:3vh}.social-media li a img{height:5vh}@media (min-width: 768px){.social-media{width:60%}.social-media li a img{height:9vh}}

button{position:relative;background-color:transparent;border:none}button .arrow{height:10vh;cursor:pointer}

.stack{height:100vh}.stack .stack-container{height:77vh;padding-top:14vh;overflow:hidden;display:flex;flex-direction:column}.stack .stack-container .stack-item h4{font-family:"Montserrat",sans-serif;color:#fff;font-weight:500;font-size:1.75rem}.stack .stack-container .stack-item ul{list-style-type:none;display:flex;justify-content:center}.stack .stack-container .stack-item ul li img{margin-top:1vh;height:5vh}.stack .stack-container .Frontend{width:60vw;-webkit-transform:translateX(100%);transform:translateX(100%);transition:all 0.5s ease-in-out;opacity:0}.stack .stack-container .Frontend.entering-from-right{-webkit-transform:translateX(-15vw);transform:translateX(-15vw);opacity:1}.stack .stack-container .Backend{width:40vw;-webkit-transform:translateX(-150%);transform:translateX(-150%);transition:all 0.5s ease-in-out;opacity:0}.stack .stack-container .Backend.entering-from-left{-webkit-transform:translateX(25vw);transform:translateX(25vw);transition-delay:0.5s;opacity:1}.stack .stack-container .Databases{width:70vw;-webkit-transform:translateX(100%);transform:translateX(100%);transition:all 0.5s ease-in-out;opacity:0}.stack .stack-container .Databases.entering-from-right{-webkit-transform:translateX(-10vw);transform:translateX(-10vw);transition-delay:1s;opacity:1}.stack .stack-container .Testing{width:35vw;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:all 0.5s ease-in-out;opacity:0}.stack .stack-container .Testing.entering-from-left{-webkit-transform:translateX(30vw);transform:translateX(30vw);transition-delay:1.5s;opacity:1}.stack .stack-container .Management{width:60vw;-webkit-transform:translateX(100%);transform:translateX(100%);transition:all 0.5s ease-in-out;opacity:0}.stack .stack-container .Management.entering-from-right{-webkit-transform:translateX(-10vw);transform:translateX(-10vw);transition-delay:2s;opacity:1}@media screen and (min-width: 768px){.stack .stack-container{height:80vh;padding-top:20vh}.stack .stack-container .stack-item h4{font-size:2.5rem}.stack .stack-container .Frontend{width:25vw}.stack .stack-container .Frontend.entering-from-right{-webkit-transform:translateX(-20vw);transform:translateX(-20vw);opacity:1}.stack .stack-container .Backend{width:15vw}.stack .stack-container .Backend.entering-from-left{-webkit-transform:translateX(20vw);transform:translateX(20vw);opacity:1}.stack .stack-container .Databases{width:25vw}.stack .stack-container .Databases.entering-from-right{-webkit-transform:translateX(-20vw);transform:translateX(-20vw);opacity:1}.stack .stack-container .Testing{width:15vw}.stack .stack-container .Testing.entering-from-left{-webkit-transform:translateX(20vw);transform:translateX(20vw);opacity:1}.stack .stack-container .Management{width:25vw}.stack .stack-container .Management.entering-from-right{-webkit-transform:translateX(-20vw);transform:translateX(-20vw);opacity:1}}

.contact{height:100vh;background:linear-gradient(180deg, #e981fc 0%, #80d8ff 60%)}.contact .contact-container{height:77vh;padding-top:17vh;display:flex;height:100%;flex-direction:column;background-image:url(http://emijerochim.com/static/media/contact.ccdd3113.svg);background-size:150%;background-position:50% 40vh;background-repeat:no-repeat}.contact .contact-container h3,.contact .contact-container p{font-family:"Montserrat",sans-serif;color:#000}.contact .contact-container h3{font-size:2rem;font-weight:600;margin-bottom:2vh;background-color:rgba(255,255,255,0.6)}.contact .contact-container p{font-size:1rem;font-weight:500;margin-top:2vh;width:85%;border-radius:10px}.contact .contact-container button{width:35vw;height:8vh;font-family:"Montserrat",sans-serif;font-weight:700;margin-top:3vh;cursor:pointer;background-color:white;border:2px solid darkslategray;border-radius:13px}@media (min-width: 768px){.contact{background:linear-gradient(90deg, #e981fc 0%, #80d8ff 60%)}.contact .contact-container{background-size:40%;background-position:95% 35vh}.contact .contact-container h3{margin-top:25vh;margin-right:50vw;font-size:4rem}.contact .contact-container p{margin-right:45vw;font-size:1.5rem;width:50%}.contact .contact-container button{margin-top:10vh;margin-right:62vw;width:15%;font-size:1.5rem}}

